.searchform-area__button {
  position: relative;
}

.search-open.deactive {
  display: none;
}

.search-close {
  position: relative;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  display: none;
  z-index: 3;
  background: var(--White);
}
.search-close.active {
  display: flex;
}
.search-form-inner {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 2;
  width: 0%;
  overflow: hidden;
  transform: translateX(100%);
  -webkit-transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out;

  &.hidden {
    transform: translateX(0);
    overflow: visible;
    width: 100%;
  }
}

.search-form {
  display: flex;
  align-items: center;
  width: 100%;
  background: var(--White);
}
.search-submit {
  width: 24px;
  height: 24px;
  background: transparent;
  border: none;
}

.search-field {
  width: 90%;
  margin-left: 24px;
  height: 26px;
  color: var(--Black);
  background: var(--White);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px; /* 162.5% */
  border: none !important;
  outline: none !important;
  &:active,
  &:focus,
  &:hover {
    border: none !important;
    outline: none !important;
  }
  &::placeholder {
    color: var(--Dark-Gray);
  }
}
.relevanssi-live-search-results-showing {
  left: 0 !important;
  top: 56px !important;
  border: none !important;
  width: 100% !important;
  -webkit-box-shadow: none !important;
  box-shadow: none !important;
}

.proinput {
  order: 2 !important;
}
.promagnifier {
  order: 1 !important;
}
.container-services {
  width: 49%;
}
.header-search-results {
  position: absolute;
  top: 56px;
  left: 0;
  width: 100%;
  background: var(--White);
  min-height: 307px;
  z-index: 2;
  -webkit-transform: scale(1, 0);
  transform: scale(1, 0);
  -webkit-transform-origin: top;
  transform-origin: top;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: start;
  -ms-flex-align: start;
  align-items: flex-start;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;

  span {
    color: var(--Dark-Gray);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 22px; /* 157.143% */
  }
}

.result-container-services {
  width: 100%;
  a {
    position: relative;
    display: flex;
    align-items: center;
    border-bottom: 1px solid #e9e9e9;
    padding: 8px 0;
    &::before {
      content: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" viewBox="0 0 16 16" fill="none"><path d="M6 12L10 8L6 4" stroke="%23B6B6B6" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"/></svg>');
      width: 16px;
      height: 16px;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
    }
  }
  img {
    width: 40px;
    height: 40px;
    margin-right: 8px;
  }
  h3 {
    margin-bottom: 2px;
    color: var(--Black);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px; /* 157.143% */
  }
  .ratemypost-container {
    span {
      color: var(--Black);
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 22px; /* 157.143% */
    }
  }
  .ratemypost-container {
    display: flex;
    align-items: center;

    span {
      display: block;
      margin-right: 4px;
      color: var(--Black);
      font-size: 14px;
      font-style: normal;
      font-weight: 400;
      line-height: 22px; /* 157.143% */
    }

    .rmp-icon--ratings {
      font-size: 16px;
      color: var(--Light-Gray);
    }
    .rmp-icon--full-highlight {
      color: var(--Yellow);
    }
  }
}
.result-container-blog {
  width: 100%;
  a {
    border-bottom: 1px solid #e9e9e9;
    padding: 8px 0;
  }
  h3 {
    margin-bottom: 8px;
    color: var(--Black);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 22px; /* 157.143% */
  }

  .result-container-blog__box {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .result-container-blog__review {
    display: flex;
    align-items: center;
    color: var(--Black);
  }
  .result-container-blog__date {
    color: var(--Dark-Gray);
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
}

.result-container-blog__color {
  display: block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-right: 4px;
}
.header-search-results-active {
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}

@media screen and (max-width: 767px) {
  .header-search-results {
    flex-direction: column;
    position: absolute;
  }
  .container-services {
    width: 100%;
    min-height: 300px;
  }
}

// !!!!!!!!!!!!!!!!!!!SEARCH PAGR!!!!!!!!!!!!!!!!!!!!!!

.search-page__title {
  margin-bottom: 50px;
  color: var(--Black);
  font-size: 36px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%; /* 43.2px */
}
.search-pag__result {
  display: flex;
  justify-content: space-between;
  margin-top: 50px;
}
.container-services {
  display: flex;
  width: 49%;
  flex-direction: column;
  gap: 12px;
  -webkit-gap: 12px;
  -moz-gap: 12px;
}
