.breadcump {
  padding: 30px 0;
  span {
    display: flex;
    align-items: center;
    gap: 8px;
    -webkit-gap: 8px;
    color: var(--Dark-Gray);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 157.143%;
  }
  a {
    color: var(--Dark-Gray);
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 157.143%;
  }
}
